import styles from './router.module.scss';
import { Outlet, Link, useLocation } from 'react-router-dom';

function Router() {
    return (
        <div className={styles.navigation}>
            <div className={styles.navs}>
                <Link to='/allcomponents'> </Link>
                <Link to='/header'></Link>
                <Link to='/totalbalance'></Link>
                <Link to='/notactive'>  </Link>
                <Link to='/graph'></Link>
                <Link to='/frens'></Link>
                <Link to='/tasks'></Link>
            </div>


            <Outlet />
        </div>
    );
}

export default Router;